"use client";

import { Filters } from "@/components/Filters";
import { MovieCard } from "@/components/MovieCard";
import { useEffect, useState } from "react";
import { getDiscoverMovies, getMovieGenres } from "@/api/tmdb";
import { Shuffle, Calendar, Trophy, X } from "lucide-react";
import { useRouter } from "next/navigation";
import clsx from "clsx";

export default function DiscoverPage() {
    const [movies, setMovies] = useState<any[]>([]);
    const [loading, setLoading] = useState(true);
    const [showRandomPicker, setShowRandomPicker] = useState(false);
    const [genres, setGenres] = useState<any[]>([]);

    // Random Picker State
    const [randomGenre, setRandomGenre] = useState("");
    const [randomRating, setRandomRating] = useState("");
    const router = useRouter();

    useEffect(() => {
        async function loadInitial() {
            setLoading(true);
            const [movieData, genreData] = await Promise.all([
                getDiscoverMovies({ sort_by: "popularity.desc" }),
                getMovieGenres()
            ]);

            if (movieData?.results) setMovies(movieData.results);
            if (genreData?.genres) setGenres(genreData.genres);
            setLoading(false);
        }
        loadInitial();
    }, []);

    const handleRandomPick = async () => {
        // Fetch a random page of movies based on filters
        const page = Math.floor(Math.random() * 50) + 1; // Random page 1-50
        const filters: any = { page };
        if (randomGenre) filters.with_genres = randomGenre;
        if (randomRating) filters["vote_average.gte"] = randomRating;

        const data = await getDiscoverMovies(filters);
        if (data?.results && data.results.length > 0) {
            const randomMovie = data.results[Math.floor(Math.random() * data.results.length)];
            router.push(`/movie/${randomMovie.id}`);
        }
    };

    return (
        <main className="min-h-screen pt-24 pb-20 bg-bg-main relative">
            <div className="container mx-auto px-6 lg:px-20 mb-10">
                <div className="flex flex-col md:flex-row md:items-center justify-between gap-6 mb-8">
                    <div>
                        <h1 className="text-4xl font-display font-bold text-white mb-2 flex items-center gap-3">
                            <span className="text-accent-primary">
                                <svg width="32" height="32" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"><circle cx="11" cy="11" r="8"></circle><line x1="21" y1="21" x2="16.65" y2="16.65"></line></svg>
                            </span>
                            Discover Movies
                        </h1>
                        <p className="text-text-secondary">Explore by era, awards, or let us pick for you</p>
                    </div>
                </div>

                {/* Feature Buttons */}
                <div className="flex flex-wrap gap-4 mb-10">
                    <button className="flex items-center gap-2 px-5 py-2.5 rounded-full bg-white/5 border border-white/10 text-white hover:bg-white/10 transition-colors">
                        <Calendar size={18} /> Browse by Era
                    </button>
                    <button className="flex items-center gap-2 px-5 py-2.5 rounded-full bg-white/5 border border-white/10 text-white hover:bg-white/10 transition-colors">
                        <Trophy size={18} /> Award Winners
                    </button>
                    <button
                        onClick={() => setShowRandomPicker(!showRandomPicker)}
                        className={clsx(
                            "flex items-center gap-2 px-5 py-2.5 rounded-full border transition-colors font-medium",
                            showRandomPicker
                                ? "bg-accent-primary border-accent-primary text-white"
                                : "bg-white/5 border-white/10 text-white hover:bg-white/10"
                        )}
                    >
                        {showRandomPicker ? <X size={18} /> : <Shuffle size={18} />}
                        {showRandomPicker ? "Close Picker" : "Random Pick"}
                    </button>
                </div>

                {/* Random Picker Section */}
                {showRandomPicker && (
                    <div className="bg-bg-card border border-white/10 rounded-2xl p-8 mb-12 animate-fade-in-up text-center relative overflow-hidden">
                        <div className="relative z-10 max-w-lg mx-auto">
                            <div className="mx-auto w-12 h-12 bg-accent-primary/20 rounded-lg flex items-center justify-center text-accent-primary mb-4">
                                <Shuffle size={24} />
                            </div>
                            <h3 className="text-2xl font-bold text-white mb-2">Random Movie Picker</h3>
                            <p className="text-text-secondary mb-8">Can't decide what to watch? Let fate decide!</p>

                            <div className="flex gap-4 mb-8">
                                <select
                                    className="flex-1 bg-black/50 border border-white/10 rounded-lg px-4 py-3 text-white focus:outline-none focus:border-accent-primary"
                                    value={randomGenre}
                                    onChange={(e) => setRandomGenre(e.target.value)}
                                >
                                    <option value="">Any Genre</option>
                                    {genres.map(g => (
                                        <option key={g.id} value={g.id}>{g.name}</option>
                                    ))}
                                </select>
                                <select
                                    className="flex-1 bg-black/50 border border-white/10 rounded-lg px-4 py-3 text-white focus:outline-none focus:border-accent-primary"
                                    value={randomRating}
                                    onChange={(e) => setRandomRating(e.target.value)}
                                >
                                    <option value="">Any Rating</option>
                                    <option value="7">7+ Good</option>
                                    <option value="8">8+ Great</option>
                                    <option value="9">9+ Masterpiece</option>
                                </select>
                            </div>

                            <button
                                onClick={handleRandomPick}
                                className="w-full py-4 bg-accent-primary hover:bg-accent-primary/90 text-white font-bold rounded-xl transition-transform hover:scale-[1.02 shadow-lg shadow-accent-primary/20"
                            >
                                <Shuffle size={20} className="inline mr-2" />
                                Pick Random Movie
                            </button>
                        </div>
                        {/* Background Splashes */}
                        <div className="absolute top-0 right-0 w-64 h-64 bg-accent-primary/5 rounded-full blur-3xl -translate-y-1/2 translate-x-1/2" />
                        <div className="absolute bottom-0 left-0 w-64 h-64 bg-accent-secondary/5 rounded-full blur-3xl translate-y-1/2 -translate-x-1/2" />
                    </div>
                )}

                <Filters />
            </div>

            <div className="container px-6 lg:px-20 mx-auto">
                {loading ? (
                    <div className="grid grid-cols-2 lg:grid-cols-5 gap-6">
                        {[...Array(10)].map((_, i) => (
                            <div key={i} className="aspect-[2/3] bg-white/5 rounded-xl animate-pulse" />
                        ))}
                    </div>
                ) : (
                    <div className="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-4 xl:grid-cols-5 gap-y-10 gap-x-6">
                        {movies.map((movie) => (
                            <MovieCard key={movie.id} movie={movie} />
                        ))}
                    </div>
                )}
            </div>
        </main>
    );
}
